
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
data:function(){
return {
isShow:false,
num:32
}
},
mounted:function(){
this.$pulick.setTitle(this,"")
let self=this;
self.isShow=true
document.title="page1"
},
components:{
Tabbar:Tabbar,
TabbarItem:TabbarItem
},
beforeRouteEnter:function(to,form,next){// 
console.log("beforeRouteEnter")
next(function(vm){//vm
console.log(vm)
})
},
afterRouteEnter:function(){
console.log("afterEach")
},
beforeRouteUpdate:function(to,form,next){// 
console.log("beforeRouteUpdate")

next()
},
beforeRouteLeave:function(to,form,next){// 
console.log("beforeRouteLeave");
next(function(){console.log(arguments)})

}
}




